import { useEffect, useState } from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	IconButton,
	Grid,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';

import { makeStyles } from '@material-ui/core/styles';
import { useQuery, useLazyQuery, gql } from '@apollo/client';
import { useHistory } from 'react-router';
import { TOffer } from '../../../types';
import graphql from '../../../graphql';

import Loader from '../../../ui-component/Loader';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import config from '../../../config';
import { getOfferPrice } from '../../../helpers/offer';
import { formatCurrency } from '../../../helpers/currency';
import OfferToolbar from './OfferToolbar';
import { formatDate } from '../../../helpers/date';
import { getValidSearchQuery } from '../../../helpers/searchFilter';
import { useSnackBar } from '../../../hooks';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

const OfferList = () => {
	const classes = useStyles();
	const history = useHistory();
	const showSnackBar = useSnackBar();

	const { loading, error, data, fetchMore } = useQuery(gql(graphql.getOffers), {
		variables: { PageIndex: 0, PageSize: config.pagination.limit },
		fetchPolicy: 'cache-first',
		notifyOnNetworkStatusChange: true,
	});

	const {
		loading: loadingStatuses,
		error: statusesError,
		data: statusesData,
	} = useQuery(gql(graphql.listOfferSteps));

	const [doSearch, setDoSearch] = useState(false);
	const [search, setSearch] = useState('');
	const [selectedStatuses, setSelectedStatuses] = useState([]);
	const [executeSearch, { loading: isSearching, data: searchData }] =
		useLazyQuery(gql(graphql.getOffers));

	const [rows, setRows] = useState<TOffer[]>([]);
	const [hasMore, setHasMore] = useState<boolean>(false);
	const [page, setPage] = useState<number>(0);

	useEffect(() => {
		const newData = doSearch ? searchData : data;
		const newCount = _get(newData, 'getOffers.Count', 0);
		const newTotal = _get(newData, 'getOffers.Total', 0);
		setHasMore(newCount < newTotal);
		setPage(_get(newData, 'getOffers.PageIndex') || 0);
		setRows(_get(newData, 'getOffers.Items') || []);
	}, [data, searchData, doSearch]);

	const isLoading = loading || isSearching;

	if ((isLoading || loadingStatuses) && !data) return <Loader />;

	if (error || statusesError) {
		showSnackBar({
			message: _get(error, 'message', 'Something went wrong'),
			alertSeverity: 'error',
		});
	}

	const editOffer = (id: string) => {
		history.push(`/offer/${id}`);
	};

	const searchHandler = (pageIndex = 0) => {
		const { query, statusIds } = getValidSearchQuery({
			search,
			selectedStatuses,
		});
		if (query) {
			executeSearch({
				variables: {
					PageIndex: pageIndex,
					PageSize: config.pagination.limit,
					Query: query.trim(),
					StepIds: statusIds,
				},
			});
		}
		setDoSearch(Boolean(query));
	};

	const loadMore = () => {
		if (doSearch) {
			searchHandler(page + 1);
			return;
		}
		fetchMore({
			variables: { PageIndex: page + 1, PageSize: config.pagination.limit },
		});
	};

	const statuses = _get(statusesData, ['listOfferSteps'], []).map(
		(status: Object) => ({
			Id: _get(status, 'id'),
			Name: _get(status, 'messages.advisor'),
		})
	);

	return (
		<>
			{isSearching && <Loader />}
			<OfferToolbar
				onSearch={searchHandler}
				onTriggerSearch={setDoSearch}
				statuses={statuses}
				search={search}
				setSearch={setSearch}
				selectedStatuses={selectedStatuses}
				setSelectedStatuses={setSelectedStatuses}
			/>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header />
					<TableBody>
						{rows.map(row => {
							if (typeof row === 'number') return null;

							return (
								<TableRow hover tabIndex={-1} key={row.OfferId}>
									<TableCell component='th'>{row.OfferNo}</TableCell>
									<TableCell align='left'>
										{_get(row, 'PropertyData.FullAddress', '')}
									</TableCell>
									<TableCell align='left'>
										{_get(row, 'PropertyData.OwnerNames', '')}
									</TableCell>
									<TableCell align='left'>{getOfferPrice(row)}</TableCell>
									<TableCell align='left'>{row.Funding?.PurchaseType}</TableCell>
									<TableCell align='left'>{row.Funding?.LoanType}</TableCell>
									<TableCell align='left'>
										{formatCurrency(row.Funding?.DownPayment)}
									</TableCell>
									<TableCell sx={{ pr: 3 }} align='left'>
										{row.Step?.messages?.advisor}
									</TableCell>
									<TableCell align='left'>{formatDate(row.CreatedAt)}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<IconButton color='primary' onClick={() => editOffer(row.OfferId)}>
											<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
										</IconButton>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{_isEmpty(rows) && !isLoading && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
			{hasMore && <LoadMore onLoadMore={loadMore} loading={isLoading} />}
		</>
	);
};

export default OfferList;
